import React from 'react';
import {
    Close as CloseIcon,
    Search as SearchIcon
} from "@mui/icons-material";
import {
    Dialog, DialogContent, DialogTitle,
    IconButton,
    Input, InputAdornment,
    Divider
} from "@mui/material";

const SearchBox = () => {
    const [open, setOpen] = React.useState(false);

    const [result, setResult] = React.useState([])

    const handleSearchClose = () => {
        setOpen(false)
    }

    return (
        <>
            <IconButton
                color="inherit"
                onClick={() => {
                    setOpen(true)
                }}
            >
                <SearchIcon/>
            </IconButton>
            <Dialog
                scroll="paper"
                onClose={handleSearchClose}
                open={open}
            >
                <DialogTitle>
                        <Input
                            placeholder="Search..."
                            startAdornment={
                                <InputAdornment position="start">
                                    <SearchIcon/>
                                </InputAdornment>
                            }
                        />
                    <IconButton
                        sx={{
                            position: 'absolute',
                            top: 0,
                            right: 0
                        }}
                        onClick={handleSearchClose}
                    >
                        <CloseIcon/>
                    </IconButton>
                </DialogTitle>
                {!!result.length && <React.Fragment>
                    <Divider />
                    <DialogContent>

                    </DialogContent>
                </React.Fragment>}

            </Dialog>
        </>
    );
};

export default SearchBox;
