import React from 'react';
import {Badge, IconButton} from "@mui/material";
import {cart} from "../../mock/header";
import {ShoppingBasket as ShoppingBasketIcon} from "@mui/icons-material";
import CartDrawer from "../CartDrawer";

const drawerWidth = 240;

const CartMenu = () => {
    const [cartOpen, setCartOpen] = React.useState(false);

    const handleCartMenuToggle = () => {
        setCartOpen(!cartOpen);
    };

    return (
        <>
            <IconButton
                color="inherit"
                onClick={handleCartMenuToggle}
            >
                <Badge
                    badgeContent={cart.length}
                    color="error"
                >
                    <ShoppingBasketIcon/>
                </Badge>
            </IconButton>
            <CartDrawer open={cartOpen} width={drawerWidth} onClose={handleCartMenuToggle}
                        onOpen={handleCartMenuToggle} items={cart}/>
        </>
    );
};

export default CartMenu;
